<template>
	<view class="container">
		<view class="title"><text>选择退款原因</text></view>

		<form @submit="" class="reason-list">
			<radio-group @change="reasonChange">
				<block v-for="(reasonItem, index) in reasonList" :key="index">
					<view class="reason-item">
						<view class="reason">{{reasonItem.reason}}</view>
						<radio class="reason-radio" :value="index.toString()" :checked="index===checkIndex" />
					</view>
				</block>
			</radio-group>
			<button type="primary" @click="eusureChange" @class="next-step">确认</button>
		</form>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				reasonList: [],
				checkIndex: 0,
				listIndex: 0,
			}
		},
		onLoad: function(options) {
			this.setData({
				reasonList: JSON.parse(uni.getStorageSync("aftersaleReasonList")),
				checkIndex: options.checkIndex,
				listIndex: options.checkIndex,
			});

		},
		methods: {
			reasonChange: function(evt) {
				this.setData({
					checkIndex: parseInt(evt.detail.value),
				});
			},

			eusureChange: function() {
				uni.navigateBack({ //返回
					success: function() {
						uni.$emit("aftersaleReasonChange", {
							listIndex: this.listIndex,
							checkIndex: this.checkIndex,
						});
					}
				});
			}
		}
	}
</script>

<style lang="less">
	page {
		background-color: #f4f4f4;
	}

	.container {
		.title {
			font-weight: 800;
			text-align: center;
			margin-top: 40rpx;
			margin-bottom: 40rpx;
		}

		.reason-list {
			font-size: 28rpx;

			.reason-item {
				padding: 30rpx;

				display: flex;
				background-color: white;

				.reason-radio {
					margin-left: auto;
				}
			}

			.next-step {
				position: absolute;
				bottom: 0;
				width: 100%
			}
		}
	}
</style>